import logo from './logo.svg';
import './App.css';
import Editor, { DiffEditor, useMonaco, loader } from "@monaco-editor/react";
import React, { useRef, useState } from "react";
import axios from "axios";
import JSZip from 'jszip';
import { saveAs } from 'file-saver';

import { SnackbarProvider, useSnackbar } from 'notistack';

function AppMain() {
  var editor = useRef(null);
  const { enqueueSnackbar } = useSnackbar();
  const [Id, setId] = useState("");
  async function getValue(event) {
    try {
      event.target.disabled = true
      editor.current.setValue("");
      var response = await axios.get('https://projects.scratch.mit.edu/' + Id);
      var data = response.data;
      if (!("targets" in data))
        throw new Error();
      if ("meta" in data)
        delete data["meta"];
      editor.current.setValue(JSON.stringify(response.data));
      editor.current.updateOptions({
        readOnly: false
      })
      setTimeout(() => {
        editor.current.getAction('editor.action.formatDocument').run();
      }, 100);
    } catch (e) {
      enqueueSnackbar("JSON\nID", { variant: "error" });
    } finally {
      event.target.disabled = false;
    }
  }
  function onExport() {
    let zip = new JSZip();
    let folder = zip.folder("project");
    folder.file('project.json', editor.current.getValue());
    zip.generateAsync({ type: "blob" })
      .then(function (content) {
        saveAs(content, "project.sb3");
      });
  }
  var Validate = (markers) => {
    if (markers.length > 0) {
      markers.forEach((x) => {
        enqueueSnackbar(`${x.startLineNumber}:${x.startColumn} ${x.message}`, { variant: "error" });
      })
    }
  }
  return (
    <>
      <h1>Scratch JSON Editor</h1>
      <div id="input-wrap">
        <span>scratch.mit.edu/projects/</span>
        <input
          type="number"
          id="id-input"
          value={Id}
          onChange={event => setId(event.target.value)}
        />
      </div>
      <div class="buttons">
        <button onClick={getValue} id="GetButton">JSON</button>
        <button onClick={onExport}></button>
      </div>
      <div className="monaco-wrap">
        <Editor
          id="Editor"
          height="500px"
          defaultLanguage="json"
          defaultValue=""
          onMount={(x) => editor.current = x}

          theme="vs-dark"
          language='ja'
          options={{
            readOnly: true
          }}
          onValidate={Validate}
        />
      </div>
    </>
  );
}
function App() {
  return (
    <SnackbarProvider maxSnack={3} preventDuplicate>
      <AppMain />
    </SnackbarProvider>
  )
}
export default App;
